


// Theme Switcher 

"use client"

import { useEffect, useState }  from "react";

const ThemeSwitcher = () => {
    // set intial state -> null
    const [is_dark, set_is_dark] = useState<boolean | null>(null);
    // listen for system theme changes
    useEffect(() => {
       const saved_theme  = localStorage.getItem("theme");

       if (saved_theme) {   // if there exists local data-theme
        document.documentElement.setAttribute("data-theme", saved_theme);
        set_is_dark(saved_theme === "dark");
       } else {             // if no data-theme, check system theme preference
        const prefers_dark = window.matchMedia('(prefers-color-scheme: dark)').matches;
        const default_theme = prefers_dark ? "dark" : "light";
        document.documentElement.setAttribute("data-theme", default_theme);
        set_is_dark(prefers_dark);
       }

       // listen for system theme changes
       const media_query = window.matchMedia('(prefers-color-scheme: dark)');
       const theme_change_handler = (e: MediaQueryListEvent) => {
        const new_theme = e.matches ? "dark" : "light";
        document.documentElement.setAttribute("data-theme", new_theme);
        set_is_dark(e.matches);
       };

       media_query.addEventListener("change", theme_change_handler);
       
       return () => {
        media_query.removeEventListener("change", theme_change_handler);
       };
    }, []);


    const switch_theme = () => {
        const new_theme = is_dark ? "light" : "dark";
        document.documentElement.setAttribute("data-theme", new_theme);
        localStorage.setItem("theme", new_theme);
        set_is_dark(!is_dark);
    }

    if (is_dark == null)    // if state is null, render a loading spinner 
        return <span className="loading loading-spinner loading-sm"></span>

    return (
        <label className="swap swap-rotate">
        {/* this hidden checkbox controls the state */}
        <input type="checkbox"
         className="theme-controller"
          value="synthwave"
          checked={is_dark}
          onChange={switch_theme} />

        {/* sun icon */}
        <svg
            className="swap-off h-10 w-10 fill-current"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24">
            <path
            d="M5.64,17l-.71.71a1,1,0,0,0,0,1.41,1,1,0,0,0,1.41,0l.71-.71A1,1,0,0,0,5.64,17ZM5,12a1,1,0,0,0-1-1H3a1,1,0,0,0,0,2H4A1,1,0,0,0,5,12Zm7-7a1,1,0,0,0,1-1V3a1,1,0,0,0-2,0V4A1,1,0,0,0,12,5ZM5.64,7.05a1,1,0,0,0,.7.29,1,1,0,0,0,.71-.29,1,1,0,0,0,0-1.41l-.71-.71A1,1,0,0,0,4.93,6.34Zm12,.29a1,1,0,0,0,.7-.29l.71-.71a1,1,0,1,0-1.41-1.41L17,5.64a1,1,0,0,0,0,1.41A1,1,0,0,0,17.66,7.34ZM21,11H20a1,1,0,0,0,0,2h1a1,1,0,0,0,0-2Zm-9,8a1,1,0,0,0-1,1v1a1,1,0,0,0,2,0V20A1,1,0,0,0,12,19ZM18.36,17A1,1,0,0,0,17,18.36l.71.71a1,1,0,0,0,1.41,0,1,1,0,0,0,0-1.41ZM12,6.5A5.5,5.5,0,1,0,17.5,12,5.51,5.51,0,0,0,12,6.5Zm0,9A3.5,3.5,0,1,1,15.5,12,3.5,3.5,0,0,1,12,15.5Z" />
        </svg>

        {/* moon icon */}
        <svg
            className="swap-on h-10 w-10 fill-current"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24">
            <path
            d="M21.64,13a1,1,0,0,0-1.05-.14,8.05,8.05,0,0,1-3.37.73A8.15,8.15,0,0,1,9.08,5.49a8.59,8.59,0,0,1,.25-2A1,1,0,0,0,8,2.36,10.14,10.14,0,1,0,22,14.05,1,1,0,0,0,21.64,13Zm-9.5,6.69A8.14,8.14,0,0,1,7.08,5.22v.27A10.15,10.15,0,0,0,17.22,15.63a9.79,9.79,0,0,0,2.1-.22A8.11,8.11,0,0,1,12.14,19.73Z" />
        </svg>
        </label>
      );
}

export default ThemeSwitcher;